"use client";

import { GameSetup } from "@/components/game-setup";
import { PreGameStart } from "@/components/pre-game-start";
import { GamePlay } from "@/components/game-play";
import { GameOver } from "@/components/game-over";
import { useGameHistory } from "@/lib/hooks/use-game-history";
import { usePendingGame } from "@/lib/hooks/use-pending-game";
import posthog from "posthog-js";
import { useEffect, useState } from "react";
import { useGameStore } from "@/lib/store-provider";

export function GameController() {
  const {
    gameSettings,
    currentLeg,
    gamePhase,
    players,
    matchId,
    activePlayerId,
    currentVisitScores,
    currentVisitDarts,
    historyLegs,
    matchWinner,
    resetGame,
  } = useGameStore((state) => state);

  const { gameHistory, addGame } = useGameHistory();
  const { savePendingGame, clearPendingGame } = usePendingGame();
  const [isInitialRender, setIsInitialRender] = useState(true);

  useEffect(() => {
    if (gamePhase !== "playing") {
      return;
    }

    const hasAnyVisits = historyLegs.some((leg) => leg.visits.length > 0);
    if (!hasAnyVisits || !matchId) {
      return;
    }

    savePendingGame({
      matchId,
      date: new Date().toISOString(),
      players,
      activePlayerId,
      gameSettings,
      currentLeg,
      currentVisitScores,
      currentVisitDarts,
      historyLegs,
    });
  }, [
    gamePhase,
    historyLegs,
    matchId,
    players,
    activePlayerId,
    gameSettings,
    currentLeg,
    currentVisitScores,
    currentVisitDarts,
    savePendingGame,
  ]);

  useEffect(() => {
    if (isInitialRender && gamePhase === "gameOver" && matchWinner !== null) {
      setIsInitialRender(false);
      clearPendingGame();

      const winnerPlayer = players.find((p) => p.id === matchWinner);
      const winnerAverage =
        winnerPlayer && winnerPlayer.dartsThrown > 0
          ? Number(
              (
                (winnerPlayer.totalScore / winnerPlayer.dartsThrown) *
                3
              ).toFixed(2),
            )
          : 0;

      posthog.capture("match_completed", {
        history_event: "match_completed",
        player_count: players.length,
        legs_played: currentLeg,
        starting_score: gameSettings.startingScore,
        out_mode: gameSettings.outMode,
        winner_average: winnerAverage,
      });

      const newGameHistory = {
        id: matchId ?? crypto.randomUUID(),
        date: new Date().toISOString(),
        players: players.map((p) => ({
          id: p.id,
          name: p.name,
          legsWon: p.legsWon,
        })),
        winner: winnerPlayer?.name || "",
        gameMode: `${gameSettings.startingScore} ${gameSettings.outMode} out`,
        legsPlayed: currentLeg,
        settings: gameSettings,
        legs: historyLegs,
      };
      addGame(newGameHistory);
    }
  }, [
    gamePhase,
    matchWinner,
    clearPendingGame,
    players,
    matchId,
    gameSettings,
    currentLeg,
    historyLegs,
    addGame,
    isInitialRender,
  ]);

  // Render the appropriate component based on game phase
  switch (gamePhase) {
    case "setup":
      return <GameSetup />;
    case "preGame":
      return <PreGameStart />;
    case "playing":
      return <GamePlay />;
    case "gameOver":
      if (matchWinner) {
        return (
          <GameOver
            winner={players.find((p) => p.id === matchWinner)!}
            gameHistory={gameHistory}
            onNewGame={resetGame}
          />
        );
      }
      // Fallback in case of error
      return <GameSetup />;
    default:
      return <GameSetup />;
  }
}
